import { mapState, mapActions } from 'ui.vue3.pinia';
import { dataStore } from '../stores/data.js';
import { smsStore } from '../stores/sms.js';

import './sms.css';

import { ControlComponent } from 'local.vue-components.control-component';
import { ButtonComponent } from 'local.vue-components.button-component';

export const Sms = {
  data() {
    return {};
  },
  components: {
    ControlComponent,
    ButtonComponent,
  },
  // language=Vue

  template: `
    <div class="vue-auth-sms-sms">
      <div class="vue-auth-sms-sms-form">
        <div class="vue-auth-sms-sms-form-body">
          <div v-for="control in smsControls" :key="control.id">
            <ControlComponent :control="control" @input="smsInput" />
            <hr />
          </div>
          <ButtonComponent :text="smsButtonSubmitTimerText || lang.AUTH_SMS_SMS_BUTTON_SUBMIT" :props="Object.keys(smsSubmitProps)" :disabled="smsButtonDisabled" @clickButton="clickSubmit" />
        </div>
      </div>
    </div>
	`,
  computed: {
    ...mapState(dataStore, ['lang']),
    ...mapState(smsStore, [
      'smsControls',
      'smsSubmitProps',
      'smsButtonDisabled',
      'smsButtonSubmitTimerText',
    ]),
  },
  methods: {
    ...mapActions(smsStore, [
      'smsInput',
      'smsRunFormSubmit',
      'smsChangeSubmitProps',
    ]),
    clickSubmit() {
      this.smsChangeSubmitProps({ 'load-circle': true });
      this.smsRunFormSubmit();
    },
  },
  mounted() {},
};
